import React, {Component} from 'react';
import './home.css';
import { Row, Col, Container, Card, Spinner, Button } from 'reactstrap';

class HomePageSection1 extends Component {
    render(){
        return(
            <React.Fragment>
            <Container>
            <Row>
                <Col md={{ size:12 }}>
                  <p className="homeHeader">{this.props.title} 
                  <a href="/aboutCOP">
          <Button className="button-lightred ml-3" size="md" style={{width: '110px', marginTop: '-10px'}} >...Know More</Button>
          </a></p>
                </Col>
            </Row>
            </Container>
            <hr/>
            <Container>
            <p className="homeSubHeader mt-5">Key Objectives</p>
            <Row className="mb-5"> 
              {this.props.homekey ? this.props.homekey.map((data) => {
                  return <Col lg={4} md={4} sm={12} xs={12}>
                  <Card className="homeKeyCard text-center">
                     <p>{data.key_title}</p>
                     <hr/>
                     <p className="mt-4">{data.key_content}</p>
                  </Card>
                </Col>
                }) : <Spinner color="danger" />
              }
            </Row>
            </Container>
            <hr/>
            <Container>
            <p className="homeSubHeader mt-5">Values</p>
            <Row className="mt-4 mb-5">
              {this.props.homevalue ? this.props.homevalue.map((data) => {
                  return <Col lg={3} md={6} sm={12} xs={12}>
                    <Card className="valuesCard">
                        <Row>
                            <Col className="homeValueNo" xs={2}>{data.value_number}</Col>
                            <Col className="homeValue pt-2" xs={10}>{data.value_text}</Col>
                        </Row>
                    </Card>
                    </Col>
                }) : <Spinner color="danger" />
              }
            </Row>
            </Container>
            </React.Fragment>
        )
    }
}

export default HomePageSection1;
