import React from 'react';
import { Col, Collapse } from 'reactstrap';
import { Card } from 'react-bootstrap';

class TeamToggle extends React.Component {
    constructor(props) {
      super(props);
      
      this.state = {collapse: this.props.key === 0 ? true : false};
    }

    toggleArrow = () => {
      this.setState({ collapse: !this.state.collapse });
    }
    
    render() {

      return (
      <Col lg={4} md={6} sm={12} xs={12}>
            <Card className="teamCard">
              <Card.Img variant="top" src={this.props.data.teamtype_img.url} style={{height: '190px', width: '70%', margin: 'auto'}}/>
              <Card.Body className="text-center">
                <Card.Title>{this.props.data.teamtype_name}</Card.Title>
               <Collapse toggler="togglerinner" isOpen={this.state.collapse}>
               <p style={{textAlign: 'justify'}}>{this.props.data.team_collapse}</p>
               </Collapse>
               <div id="togglerinner" onClick={this.toggleArrow}>{this.state.collapse ? 
               <i class="fa fa-angle-double-up fa-2x"></i> : <i class="fa fa-angle-double-down fa-2x"></i>} 
               </div>
              </Card.Body>
            </Card>
        </Col>
      );

    }
}
  
export default TeamToggle;


