import React from 'react';
import { Collapse, Row, Col, Container, Form, Table, Button, CardBody, Card, Alert, FormGroup, Input, Label, } from 'reactstrap';
import Picky from "react-picky";
import { AvForm, AvField } from 'availity-reactstrap-validation';
import * as actions from '../../redux/actions/actions';
import { connect } from 'react-redux';
import DatePicker from "react-date-picker";

class CreditPartnerCard extends React.Component {
    constructor(props) {
      super(props);
      
      this.state = {collapse: this.props.keyValue === 0 ||  this.props.keyValue ? true : false};
    }

    toggleCard = () => {
      this.setState({ collapse: !this.state.collapse });
    }

    checkRegisterFormDetails = () => {

        let flag = null;
        if(flag || flag === null){
                if(this.props.pdata.designation !== "" && this.props.pdata.firstName !== "" && this.props.pdata.lastName !== "" 
                && this.props.pdata.gender !== "" && this.props.pdata.dob !== "" ) {
                    flag = true;
                }
                else {
                    flag = false;
                }
        }
        console.log(flag);
        return flag;
    }
    
    render() {

      console.log(this.props.stdata);
      
      return (
        <Row > 
        <Col md={12} className="mt-3">
        <div className="profile-tab" id="togglerinner" onClick={this.toggleCard}>
        {this.state.collapse ? <i class="fa fa-minus mr-1"></i> : <i class="fa fa-plus mr-1"></i>}
        {this.props.pdata.firstName ? this.props.pdata.firstName + ' ' + this.props.pdata.lastName
        : 'Patient' + ' ' + (this.props.keyValue+1) } 

        {this.props.pdata.validationMessages ? <span style={{color: 'red', fontWeight: 500}}>{' - ' + this.props.pdata.validationMessages}</span> : null }
        
        <i class="fa fa-close mr-1 mt-1 pull-right" onClick={() => this.props.removePatient(this.props.pdata, this.props.keyValue)}></i> </div>
        
        <Collapse toggler="togglerinner" isOpen={this.state.collapse}>
            <Card className="collapse-box">
        <CardBody>
        <Row>
        <Col md={4} className="mt-3">
        <FormGroup>
            <Label for="title"><b><span className="starSize">*</span>Title</b></Label>
            <Input type="select" name="designation" value={this.props.pdata.designation} placeholder="Select" onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)}>
                <option value="">Select Title</option>
                <option value="Mr">Mr.</option>
                <option value="Mrs">Mrs.</option>
                <option value="Miss">Miss.</option>
                <option value="Ms">Ms.</option>
                <option value="Master">Master</option>
                <option value="Dr">Dr.</option>
            </Input>
        </FormGroup>
        </Col>
        <Col md={4} className="mt-3">
        <FormGroup>
            <Label for="fname"><b><span className="starSize">*</span>First Name</b></Label>
            <Input type="text" name="firstName" value={this.props.pdata.firstName} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)} placeholder="First Name" />
        </FormGroup>
    </Col>
    <Col md={4} className="mt-3">
        <FormGroup>
            <Label for="lname"><b><span className="starSize">*</span>Last Name</b></Label>
            <Input type="text" name="lastName" value={this.props.pdata.lastName} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)} placeholder="Last Name" />
        </FormGroup>
    </Col>
    </Row>
    <Row>
    <Col md={4} style={{marginTop: '35px'}}>
        <FormGroup>
            <Label for="email"><b>Email</b></Label>
            <Input type="email" name="email" value={this.props.pdata.email} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)} placeholder="Email Id" />
        </FormGroup>
    </Col>
    <Col md={4} style={{marginTop: '35px'}}>
        <FormGroup>
            <Label for="mobile"><b>Mobile Number</b></Label>
            <Input type="number" name="mobile" value={this.props.pdata.mobile} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)} placeholder="Mobile Number" />
            <p style={{color: 'red'}}>{this.props.pdata.validationMessages}</p>
            <p className="red-font mt-3">{this.props.noValidationMessages}</p>
        </FormGroup>
    </Col>
    <Col md={4} className="mt-3">
    <Label for="state"><b><span className="starSize">*</span>Date of Birth</b></Label>
        <FormGroup>
        <DatePicker
          value={this.props.pdata.dob}
          onChange={(date) => this.props.handleDOBChange(date, this.props.keyValue)}
          maxDate={new Date()}
        />
        {/* <Input type="date" name="dob" value={this.props.pdata.dob} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)} /> */}
        </FormGroup>
      {/* <AvForm>
        <AvField name="dateRange" label="Date of Birth" type="date" validate={{dateRange: {start: {value: -999, units: 'years',} ,end: {value: -1, units: 'months',}}}} />
      </AvForm> */}
    </Col>
    </Row>
    <Row>
    <Col md={4} className="mt-3">
    <FormGroup tag="fieldset">
        <legend className="col-form-label"><b><span className="starSize">*</span>Gender</b></legend>
        <Col>
        <FormGroup check>
            <Label check>
            <Input type="radio" name={'gender' + this.props.keyValue} value="Male" checked={this.props.pdata.gender === "Male"} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)}/>
            Male
            </Label>
        </FormGroup>
        <FormGroup check>
            <Label check>
            <Input type="radio" name={'gender' + this.props.keyValue} value="Female" checked={this.props.pdata.gender === "Female"} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)}/>
            Female
            </Label>
        </FormGroup>
        <FormGroup check >
            <Label check>
            <Input type="radio" name={'gender' + this.props.keyValue} value="Other" checked={this.props.pdata.gender === "Other"} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)}/>
            Other
            </Label>
        </FormGroup>
        </Col>
    </FormGroup>
    </Col>
    {/* <Col md={8} className="mt-3">
        <FormGroup>
            <Label for="mobile"><b>Address</b></Label>
            <Input type="text" name="Address" value={this.props.pdata.Address} onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue)} placeholder="Address" />
        </FormGroup>
    </Col> */}
    </Row>
    {this.checkRegisterFormDetails(this.state.form) ?
      null :
      <p style={{color: 'red'}} className="mt-3">Fields marked with  <span className="starSize"> * </span>  are required.</p>
    }
    <hr/>
    {this.props.pdata.testList ? this.props.pdata.testList.map((item, key1) => 
    <Row>
    <Col md={1}>
        <i class="fa fa-trash mr-1 pr-3 pt-5 pb-5 pull-right" style={{fontSize: '20px'}} onClick={() => this.props.removeSample(this.props.pdata, this.props.keyValue, key1)}></i>
    </Col>
    {this.props.stdata ?
    <Col md={3} className="mt-3">
        <FormGroup>
            <Label for="exampleSelect"><b>Select Department:</b></Label>
                <Input type="select" name="selectedSample" id="sampleSelect" onChange={(event) => this.props.inputChangedPatientHandler(event, this.props.keyValue, key1)}>
                <option>Select Sample</option>
                {this.props.stdata.map((el) => <option value={el.id}>{el.sampleName}</option> )}    
                </Input>
        </FormGroup>
    </Col>
    : null }
    {this.props.stestdata ?
    <Col md={3} className="mt-3">
            <Label for="exampleSelect" id="selectTestLabelCP"><b>Select Tests:</b></Label>
            <div>
            <Picky
                value={item.testListArr}
                options={this.props.stestdata}
                onChange={(undefined) => {this.props.selectPatientMultipleOption(undefined, this.props.keyValue, key1); document.getElementById("selectTestLabelCP").click()}}
                tabIndex={this.props.keyValue}
                open={false}
                keepOpen={false}
                valueKey="id"
                labelKey="testName"
                multiple={true}
                includeSelectAll={true}
                includeFilter={true}
                dropdownHeight={600}
                numberDisplayed={1}
                manySelectedPlaceholder="%s selected"
                allSelectedPlaceholder="%s selected"
            />
            </div>
            
    </Col>
    : null }
    <Col md={5} className="mt-3">
        {item.testListArr ? item.testListArr.length !== 0 ? <Table bordered>
            <thead>
                <tr>
                <th>Sr No.</th>
                <th>Test Name</th>
                <th>Test Charges</th>
                </tr>
            </thead>
            <tbody>
                {item.testListArr.map((test, key) => 
                <tr>
                <td>{key + 1}</td>
                <td>{test.testName}</td>
                <td>{test.price}</td>
                </tr>)
                }
                {(item.testTotalCharge) ?
                <tr>
                <td colSpan={2}><b>Total</b></td>
                <td><b>&#8377; {(item.testTotalCharge).toLocaleString(navigator.language, { minimumFractionDigits: 0 })}</b></td> 
                </tr> 
                : null }
            </tbody>
        </Table> : null 
        : null
        }
    </Col>
    
</Row>

): null }


    
<Row>
    <Col md={12} className="mt-3 text-right">
        <Button className="button-lightred" size="md" style={{width: '190px'}}  onClick={() => this.props.addSample(this.props.pdata, this.props.keyValue)}>Add Test(s)</Button>{' '}
    </Col>
</Row>
                </CardBody>
            </Card>
        </Collapse>

        
    </Col>
    </Row>
      );
    }
}

const mapStateToProps = state => {
    return {
        loading: state.loading,
        user: state.user,
        stestsuccess: state.stestsuccess,
        stestdata: state.stestdata,
    }
}
    
const mapDispatchToProps = dispatch => {
    return {
        onSampleTest: (id) => dispatch(actions.sampleTest(id)),
    }
}
      
export default connect(mapStateToProps, mapDispatchToProps)(CreditPartnerCard);


